import {Component, OnInit} from '@angular/core';
import {Pageable} from './pageable';

@Component({
  selector: 'app-welcome',
  templateUrl: './welcome.component.html',
  styleUrls: ['./welcome.component.scss']
})
export class WelcomeComponent implements OnInit {

  pageable:Pageable;
  anchors;
  constructor() {

  }

  ngOnInit() {

    this.pageable = new window['Pageable']('#container', {
      animation: 400,
      // onInit: this.init(),
      onFinish: this.update,
      events: {
        mouse: false
      }
    });
    console.log(this.pageable);
    this.pageable.initialised = false;
    this.pageable.update();

  }

  gotoSection(number: number) {
    this.pageable.scrollToPage(3)
  }

  private init() {

  }

  private update(data) {
    console.log('updated', data)
  }
}
export interface PageableEvents{
  wheel: boolean;
  mouse: boolean;
  touch: boolean;
  keydown: boolean;
}
export interface PageableOptions {
  pips: boolean;
  animation: number;
  delay: number;
  throttle: number;
  orientation: string;
  easing: Function,
  onInit: Function,
  onUpdate: Function,
  onBeforeStart: Function,
  onStart: Function,
  onScroll: Function,
  swipeThreshold: number,
  freeScroll: boolean,
  slideshow: boolean,
  infinite: boolean,
  childSelector: string,
  events: PageableEvents
}
export interface PageableAxis{
  x: string;
  y: string;
}

export interface Callbacks {
  wheel: any;
  update: any;
  load: any;
  start: any;
  drag: any;
  stop: any;
  click: any;
  prev: any;
  next: any;
  keydown: any;
}

export interface Size{
  width: number;
  height: number;
}

export interface PageableData{
  window: Size,
  container: Size
}

